import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';

import './index.scss';
import Avatar from '../../ui/Avatar';
import Label, { LabelTypography, LabelColors, LabelStringSet } from '../../ui/Label';
import Modal from '../../ui/Modal';
import { Type as ButtonTypes } from '../../ui/Button/type';
import Checkbox from '../../ui/Checkbox';

const filterUser = (idsToFilter) => (currentId) => idsToFilter.includes(currentId);

const InviteMembers = (props) => {
  const {
    userQueryCreator,
    closeModal,
    onSubmit,
    submitText,
    titleText,
    idsToFilter,
    swapParams,
  } = props;

  const [users, setUsers] = useState([]);
  const [selectedUsers, setSelectedUsers] = useState({});
  const [usersDataSource, setUsersDataSource] = useState({});

  const selectedCount = Object.keys(selectedUsers).length;

  useEffect(() => {
    const applicationUserListQuery = userQueryCreator();
    setUsersDataSource(applicationUserListQuery);
    applicationUserListQuery.next((res, err) => {
      // eslint-disable-next-line no-underscore-dangle
      let users_ = res;
      let error = err;
      if (swapParams) {
        users_ = err;
        error = users_;
      }
      if (error) {
        return;
      }
      setUsers(users_);
    });
  }, []);

  return (
    <Modal
      onCancel={closeModal}
      onSubmit={() => {
        const selectedUserList = Object.keys(selectedUsers);
        if (selectedUserList.length > 0) {
          onSubmit(selectedUserList);
          closeModal();
        }
      }}
      submitText={submitText}
      titleText={titleText}
      type={ButtonTypes.PRIMARY}
    >
      <div>
        <Label
          color={(selectedCount > 0) ? LabelColors.PRIMARY : LabelColors.ONBACKGROUND_3}
          type={LabelTypography.CAPTION_1}
        >
          {`${selectedCount} ${LabelStringSet.MODAL__INVITE_MEMBER__SELECTEC}`}
        </Label>
        <div
          className="sendbird-create-channel--scroll"
          onScroll={(e) => {
            const { hasNext } = usersDataSource;
            const fetchMore = (
              e.target.clientHeight + e.target.scrollTop === e.target.scrollHeight
            );

            if (hasNext && fetchMore) {
              usersDataSource.next((usersBatch, error) => {
                if (error) {
                  return;
                }
                setUsers([
                  ...users,
                  ...usersBatch,
                ]);
              });
            }
          }}
        >
          {
            users.map((user) => (!filterUser(idsToFilter)(user.userId)) && (
              <div key={user.userId} className="sendbird-create-channel--user-row">
                <div className="sendbird-create-channel--user-row--avatar">
                  <Avatar height="40px" width="40px" src={user.profileUrl} />
                </div>
                <div className="sendbird-create-channel--user-row--label">
                  <Label className="sendbird-create-channel--user-row--label__nickname" type={LabelTypography.SUBTITLE_1} color={LabelColors.ONBACKGROUND_1}>
                    {user.nickname || LabelStringSet.NO_NAME}
                  </Label>
                  {
                    user.nickname
                      ? null
                      : (
                        <Label className="sendbird-create-channel--user-row--label__user-id" type={LabelTypography.CAPTION_3} color={LabelColors.ONBACKGROUND_2}>
                          {user.userId}
                        </Label>
                      )
                  }
                </div>
                {/* eslint-disable-next-line jsx-a11y/label-has-associated-control */}
                <label htmlFor={user.userId} className="sendbird-create-channel--user-row--checkbox">
                  <Checkbox
                    id={user.userId}
                    checked={selectedUsers[user.userId]}
                    onChange={
                      (event) => {
                        const modifiedSelectedUsers = {
                          ...selectedUsers,
                          [event.target.id]: event.target.checked,
                        };

                        if (!event.target.checked) {
                          delete modifiedSelectedUsers[event.target.id];
                        }

                        setSelectedUsers(modifiedSelectedUsers);
                      }
                    }
                  />
                </label>
              </div>
            ))
          }
        </div>
      </div>
    </Modal>
  );
};

InviteMembers.propTypes = {
  idsToFilter: PropTypes.arrayOf(
    PropTypes.string,
  ),
  swapParams: PropTypes.bool,
  userQueryCreator: PropTypes.func.isRequired,
  closeModal: PropTypes.func.isRequired,
  onSubmit: PropTypes.func.isRequired,
  submitText: PropTypes.string,
  titleText: PropTypes.string,
};
InviteMembers.defaultProps = {
  swapParams: false,
  submitText: 'create',
  titleText: 'Create new channel',
  idsToFilter: [],
};

export default InviteMembers;
